.app-wrapper {
    display: grid;
    grid-template-areas:
          'h h '
          'n c';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    margin: 0 auto;
    width: 1200px;
}


.header {
    grid-area: h;
    background-color: #6ade74;
}

.header img {
    width: 25px;
}

.nav {
    grid-area: n;
    background-color: #f86666;
}

.content {
    grid-area: c;
    background-color: #46bbe5;
}

ul li {
    list-style: none;
}
